<template>
    <div>
        <div v-if="currentPage === 'login'">
            <Login @changeCurrentPage='changeCurrentPage'></Login>
        </div>
        <div v-else-if="currentPage === 'register'">
            <Register @changeCurrentPage='changeCurrentPage'></Register>
        </div>
        <div v-else-if="currentPage === 'home'">
            <Home :all-articles='allArticles' :my-articles='myArticles' @changeCurrentPage='changeCurrentPage' @fetchArticle='fetchArticle' @myArticleSearch='myArticleSearch' @searchAllArticleTitle="searchAllArticleTitle" @searchAllArticleTag='searchAllArticleTag'></Home>
        </div>
    </div>
</template>

<script>
import server from './api/helper'

const { axios , Swal , serverURL } = server
import Login from "./components/Login"
import Register from "./components/Register"
import Home from "./components/Home"
export default {
    data : function () {
        return {
            allArticles : [],
            myArticles : [],
            currentPage : "login"
        }
    },
    components : {
        Login,
        Register,
        Home
    },
    methods : {
        fetchAllArticles() {
            const token = localStorage.getItem('token');
            axios({
                method : "GET",
                url : `${serverURL}/articles/`,
                headers : {
                    token
                }
            })
            .then( response => {
                this.allArticles = response.data.articles
            })
            .catch( err => {
                console.log('FETCH ALL ARTICLES ERROR');
                console.log( err.response ) ;
                this.$buefy.toast.open({
                    duration: 5000,
                    message: `Oppss... Somthing gone can't fetch all article please reload!`,
                    type: 'is-danger'
                })  
            })
        },
        fetchUserArticles() {
            const token = localStorage.getItem('token');
            axios({
                method: "GET",
                url : `${serverURL}/articles/user`,
                headers : {
                    token
                }
            })
            .then ( response => {
                this.myArticles = response.data.articles;
                if ( this.myArticles.length != 0)
                    localStorage.setItem('userId' , this.myArticles[0].userId._id )
            })
            .catch( err => {
                console.log('FETCH USER ARTICLES ERROR');
                console.log( err );
                this.$buefy.toast.open({
                    duration: 5000,
                    message: `Oppss... Somthing gone can't fetch user article please reload!`,
                    type: 'is-danger'
                })  
            })
           
        },
        fetchArticle() {
            this.fetchAllArticles();
            this.fetchUserArticles();
        },
        showError( title , message ) {
            Swal.fire('error' , title, message )
        },
        showSuccess ( title , message ) {
            Swal.fire('success', title, message )
        },
        changeCurrentPage ( page ) {
            this.currentPage = page;
        },
        myArticleSearch( keyword) {
            const token = localStorage.getItem('token');
            axios({
                method:"GET",
                url : `${serverURL}/articles/user/search?title=${keyword}`,
                headers : {
                    token 
                }
            })
            .then ( response => {
                this.myArticles = response.data.articles;
            })
            .catch ( err => {
                console.log("MY ARTICLE SEARCH ERRr");
                console.log( err.response )
                this.$buefy.toast.open({
                    duration: 5000,
                    message: `Oppss... Somthing gone can't search article please reload!`,
                    type: 'is-danger'
                })  
            })
        },
        searchAllArticleTitle( keyword ) {
            const token = localStorage.getItem('token');
            axios({
                method:"GET",
                url : `${serverURL}/articles/search?title=${keyword}`,
                headers : {
                    token 
                }
            })
            .then ( response => {
                console.log( response.data.articles )
                this.allArticles = response.data.articles;
            })
            .catch ( err => {
                console.log("MY ARTICLE SEARCH BY TITLE ERRr");
                console.log( err.response )
                this.$buefy.toast.open({
                    duration: 5000,
                    message: `Oppss... Somthing gone can't search by title please reload!`,
                    type: 'is-danger'
                })  
            })
        },
        searchAllArticleTag( tags ) {
            const token = localStorage.getItem('token');
            axios({
                method:"POST",
                url: `${serverURL}/articles/tag`,
                data : { tags },
                headers : {
                    token
                }
            })
            .then ( response => {
                this.allArticles = response.data.articles;
            })
            .catch ( err => {
                console.log("MY ARTICLE SEARCH BY TAG ERRr");
                console.log( err.response )
                this.$buefy.toast.open({
                    duration: 5000,
                    message: `Oppss... Somthing gone can't search by tag please reload!`,
                    type: 'is-danger'
                })  
            })
        }
    },
    created : function () {
        const token = localStorage.getItem('token')
        if ( token ) {
            this.changeCurrentPage('home')

            this.fetchAllArticles();
            this.fetchUserArticles();
        } else {
            this.changeCurrentPage('login')
        }
    }

}
</script>

